
.ant-btn{
    border: 0;
}
.ant-btn:hover{
    opacity: 0.5;
}

.ant-checkbox-inner{
    border-radius: 15px;
    width: 30px;
    height: 30px;
    text-align: center;
}
.todos-header{
    text-align: center;
    position: relative;
    width: 100%;
}
.todos-header h1{
    text-align: center;
    position: absolute;
    color:rgba(175, 47, 47, 0.15);
    font-size: 100px;
    font-weight: 100;
    width: 100%;
    top: -130px;
}
.todos-header .new-todo{
    font-size: 24px;
    width: 100%;
    line-height: 1.4em;
    box-sizing: border-box;
    border: none;
    padding: 16px 16px 16px 60px;
    box-shadow: inset 0 -2px 1px rgb(0 0 0 / 3%);
}
.todos-header .new-todo::placeholder{
    font-style: italic;
    font-weight: 300;
    color: #e6e6e6;
}
.todos-main{
    width: 100%;
}
.complete-all{
    position: absolute;
    top: 30px;
    left: 0px;
    width: 40px;
    height: 40px;
    transform: rotate(90deg);
    font-size: 20px;
    cursor: pointer;
    opacity: 0.5;
}

.todos-main ul{
    list-style: none;
    margin-bottom: 0;
    padding-left: 0;
}
.todos-main ul li{
    display:block;
    position: relative;
    font-size: 24px;
}
.todos-main ul li .edit{
    position: absolute;
    top: 0;
    padding: 10px 40px 10px 60px;
    font-size: 24px;
    display: none;
    z-index: 3;
}
.todos-main ul li .edit-btn{
    position: absolute;
    top: 10px;
    right: 10px;
    display: none;
}
.todos-main ul .editing .edit,
.todos-main ul .editing .edit-btn
{
    display: block;
    z-index: 3;
}
.todos-main ul .content .completed {
    color: #d9d9d9;
    text-decoration: line-through;
}
.todos-main ul li .complete,
.del{
    width: 40px;
    height: 40px;
    text-align: center;
    position: absolute;
    z-index: 2;
}
.complete{
    left: 10px;
    top: 10px;
}
.del{
    width: 80px;
    right: 0;
    top: 10px;
}
.content{
    width: 100%;
    padding: 15px 15px 15px 60px;
    position: relative;
    top: 0;
    right: 0;
    word-break: break-all;
    line-height: 1.2;
}
.todos-main ul li .back{

}
.todos-footer{
    position: relative;
    width: 100%;
    color: #777;
    padding: 10px 15px;

}
.todos-footer span{
    float: left;
}
.todos-footer ul{
    width: 100%;
    list-style: none;
    text-align: center;
    margin-bottom: 0;
    position: absolute;
    right: 0;
}
.todos-footer ul li {
    display: inline;
}
.todos-footer ul li a{
    color: inherit;
    margin: 3px;
    padding: 3px 7px;
    border: 1px solid transparent;
}
.todos-footer ul li a:hover{
    border-color: rgba(175, 47, 47, 0.1);
}
.todos-footer:before {
    content: '';
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 50px;
    overflow: hidden;
    box-shadow: 0 1px 1px rgb(0 0 0 / 20%), 0 8px 0 -3px #f6f6f6, 0 9px 1px -3px rgb(0 0 0 / 20%), 0 16px 0 -6px #f6f6f6, 0 17px 2px -6px rgb(0 0 0 / 20%);
}
